import photo from '../assets/images/profile.jpg'

const ProjectsPage = () => (
  <Layout>
    <SEO
      title="Projects"
      description="Projects developed for study"
      keywords="React, React JS, React Native, Node, Front-end, Back-end, API, developer"
    />
    <section className="section content is-size-4-desktop is-size-5-touch">
      <Title>Projects</Title>
      <p className="has-text-centered">
        Some of my featured projects, see more on my{' '}
        <a href="https://github.com/leandrodsi">GitHub</a>.
      </p>
      <div className="columns is-centered">
        <div className="column is-half">
          <Card
            title="Project #1"
            subtitle="Build your personal page using Gatsby"
            link="https://www.google.com"
            tags={['React', 'Gatsby']}
            image={photo}
          />
          <Card
            title="Project #1"
            subtitle="Build your personal page using Gatsby"
            link="https://www.google.com"
            tags={['React', 'Gatsby']}
            image={photo}
          />
          <Card
            title="Project #1"
            subtitle="Build your personal page using Gatsby"
            link="https://www.google.com"
            tags={['React', 'Gatsby']}
            image={photo}
          />
        </div>
      </div>
      <p className="has-text-centered">
        Check out my <Link to="/blog">blog</Link>.
      </p>
    </section>

    <Link to="/">Go back to the homepage</Link>
  </Layout>
)

export default ProjectsPage
